/*設定*/

$modifier-prefix: --;
$element-prefix: __;
$default-side-margin: no;
$size-divider: -;

/*基本テキストサイズ*/
$base-text-size-px: 13;
$base-text-size: $($base-text-size-px)px;

/*基本テキストカラー*/
$base-text-color: #333;

/*基本リンクテキストカラー*/
$base-link-color: #074ae2;

/*基本フォント*/

@font-face {
  font-family: "MyYuGothicM";
  font-weight: normal;
  src: local("YuGothic-Medium"), /* Postscript name */
  local("Yu Gothic Medium"), /* for Chrome */
  local("YuGothic-Regular"); /* Mediumがない場合 */
}

@font-face {
  font-family: "MyYuGothicM";
  font-weight: bold;
  src: local("YuGothic-Bold"), /* Postscript name */
  local("Yu Gothic"); /* for Chrome */
}

$base-font-family:
  'Noto Sans CJK JP',
  'Noto Sans CJK Japanese',
  MyYuGothicM, /* Windows 游ゴシック */
  YuGothic, /* OS X 游ゴシック */
  -apple-system, /* OS X, iOS San Francisco */
  BlinkMacSystemFont, /* OS X, iOS Chrome San Francisco */
  "Hiragino Kaku Gothic ProN", /* OS X, iOS ヒラギノ */
  Meiryo, /* Windows メイリオ */
  sans-serif;

/*出力するグリッドの隙間*/
$gaps: 0, 10, 20;

/*スクリーンサイズ一覧*/
$screen-size-pc: 960px;
$screen-size-pc-max: 959px;
$screen-size-tablet: 720px;
$screen-size-tablet-max: 719px;
$screen-size-breakpoint-1: 640px;
$screen-size-mobile: 480px;
$screen-size-mobile-max: 479px;

$screen-size:
  $screen-size-pc,
  $screen-size-pc-max,
  $screen-size-tablet,
  $screen-size-tablet-max,
  $screen-size-breakpoint-1,
  $screen-size-mobile,
  $screen-size-mobile-max;

$screen-size-name:
  pc,
  pc-max,
  tablet,
  tablet-max,
  breakpoint-1,
  mobile,
  mobile-max;
